import styled from "styled-components";
import ArticleItem from "./ArticleItem";
import NewsItem from "./NewsItem";
import KeyboardArrowLeftIcon from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import { useDispatch, useSelector } from "react-redux";
import {
  selectActiveBlogCategories,
  setActiveBlogCategories,
} from "features/activeBlogCategoriesSlice";
import { slice } from "lodash";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";
import { useEffect, useState } from "react";
import newslist from "mockup/news";
import articleslist from "mockup/articles";
import lessnewslist from "mockup/lessnewslist";

const MoreHorizIconCustom = styled(MoreHorizIcon)`
  cursor: pointer;
`;

const KeyboardArrowRightIconCustom = styled(KeyboardArrowRightIcon)`
  padding: 3px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  margin-right: 3px;
  border-radius: 3px;
  cursor: pointer;
  &:hover {
    border: 1px solid rgba(0, 0, 0, 0.09);
  }
`;

const KeyboardArrowLeftIconCustom = styled(KeyboardArrowLeftIcon)`
  padding: 3px;
  border: 1px solid rgba(0, 0, 0, 0.05);
  margin-right: 3px;
  border-radius: 3px;
  cursor: pointer;
  &:hover {
    border: 1px solid rgba(0, 0, 0, 0.09);
  }
`;

function Blogs() {
  const [post, setPost] = useState([]);
  const [isCompleted, setIsCompleted] = useState(false);
  const [index, setIndex] = useState(3);
  const initialPosts = slice(post, 0, index);
  const getData = () => {
    setPost(newslist);
  };
  const loadMore = () => {
    setIndex(index + 3);
    console.log(index);
    if (index >= post.length) {
      setIsCompleted(true);
    } else {
      setIsCompleted(false);
    }
  };
  useEffect(() => {
    getData();
  }, []);

  let dispatch = useDispatch();
  let activeCategories = useSelector(selectActiveBlogCategories);
  console.log(activeCategories);
  function handleToggleActiveCategory(category) {
    if (activeCategories.includes(category)) {
      dispatch(
        setActiveBlogCategories(
          activeCategories.filter(
            (activeCategory) => activeCategory !== category
          )
        )
      );
    } else {
      dispatch(setActiveBlogCategories([...activeCategories, category]));
    }
  }
  return (
    <div className="row">
      <div className="col-12 px-4">
        <div className="row">
          <div className="col-12 pt-3">
            <TitleWrapper>
              <Title>Recent Article</Title>

              <span>
                <KeyboardArrowLeftIconCustom />
                <KeyboardArrowRightIconCustom />
              </span>
            </TitleWrapper>
          </div>
          <div className="row">
            <div className="col-12">
              <div className="row">
                {articleslist.map((d) => {
                  return <ArticleItem data={d} />;
                })}
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <Title>Categories</Title>
            <div className="row">
              <CategoryWrapper className="col-12 py-4">
                <CategoryItem>
                  <CategoryBox
                    style={{
                      background: "url('/globaladmissions/images/c1.png')",
                    }}
                  >
                    <img
                      alt=""
                      src="/globaladmissions/images/categories/v1.png"
                    />
                  </CategoryBox>
                  <CategoryDescription>MBA</CategoryDescription>
                </CategoryItem>
                <CategoryItem>
                  <CategoryBox
                    style={{
                      background: "url('/globaladmissions/images/c2.png')",
                    }}
                  >
                    <img
                      alt=""
                      src="/globaladmissions/images/categories/v2.png"
                    />
                  </CategoryBox>
                  <CategoryDescription>MBBS</CategoryDescription>
                </CategoryItem>
                <CategoryItem>
                  <CategoryBox
                    style={{
                      background: "url('/globaladmissions/images/c3.png')",
                    }}
                  >
                    <img
                      alt=""
                      src="/globaladmissions/images/categories/v3.png"
                    />
                  </CategoryBox>
                  <CategoryDescription>Universities</CategoryDescription>
                </CategoryItem>
                <CategoryItem>
                  <CategoryBox
                    style={{
                      background: "url('/globaladmissions/images/c4.png')",
                    }}
                  >
                    <img
                      alt=""
                      src="/globaladmissions/images/categories/v4.png"
                    />
                  </CategoryBox>
                  <CategoryDescription>Student Experiences</CategoryDescription>
                </CategoryItem>
                <CategoryItem>
                  <CategoryBox
                    style={{
                      background: "url('/globaladmissions/images/c5.png')",
                    }}
                  >
                    <img
                      alt=""
                      src="/globaladmissions/images/categories/v5.png"
                    />
                  </CategoryBox>
                  <CategoryDescription>Language programs</CategoryDescription>
                </CategoryItem>
              </CategoryWrapper>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <Title>All News</Title>
          </div>
          <CategoryBar>
            <Category
              onClick={(e) => {
                handleToggleActiveCategory("case-study");
              }}
              active={activeCategories.includes("case-study")}
            >
              Case Study
            </Category>
            <Category
              onClick={(e) => {
                handleToggleActiveCategory("programs");
              }}
              active={activeCategories.includes("programs")}
            >
              Programs
            </Category>
            <Category
              onClick={(e) => {
                handleToggleActiveCategory("mbbs");
              }}
              active={activeCategories.includes("mbbs")}
            >
              Mbbs
            </Category>
            <Category
              onClick={(e) => {
                handleToggleActiveCategory("mba");
              }}
              active={activeCategories.includes("mba")}
            >
              Mba
            </Category>
            <Category
              onClick={(e) => {
                handleToggleActiveCategory("universities");
              }}
              active={activeCategories.includes("universities")}
            >
              Universities
            </Category>
            <Category>
              <MoreHorizIconCustom />
            </Category>
          </CategoryBar>
          <div className="row">
            <div className="col-12">
              <div className="row">
                {lessnewslist.map((d) => {
                  return <NewsItem data={d} />;
                })}

                <CovidBlock>
                  <span>No More Covid Now</span>

                  <Btn style={{ textTransform: "capitalize" }}>Apply Now</Btn>
                </CovidBlock>

                {initialPosts.map((item, id) => {
                  return <NewsItem data={item} />;
                })}
                <div className="d-grid mt-3 mb-5">
                  {isCompleted ? (
                    <button
                      onClick={loadMore}
                      type="button"
                      className="btn btn-danger disabled"
                    >
                      That's It
                    </button>
                  ) : (
                    <button
                      onClick={loadMore}
                      type="button"
                      className="btn btn-danger"
                    >
                      Show More +
                    </button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Blogs;

const Category = styled.span`
  border: 1px solid rgba(0, 0, 0, 0.09);
  padding: 4px 2px;
  flex: 1;
  margin: 4px 4px;
  border-radius: 20px;
  cursor: pointer;
  text-align: center;
  min-width: 100px;
  background: ${(props) => (props.active ? `blue` : `white`)};
  color: ${(props) => (props.active ? `white` : ``)};
`;

const CategoryBar = styled.div`
  padding: 2px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
`;

const Btn = styled.button`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 5px 24px 5px;
  gap: 10px;
  border: none;
  background: radial-gradient(
    62.26% 123.96% at 69.08% 44.79%,
    #1d4ed8 0%,
    #1741b7 100%
  ) !important;
  box-shadow: 0px 4px 8px rgba(2, 14, 47, 0.1), inset 0px 1px 0px #1741b7,
    inset 0px 2px 0px rgba(255, 255, 255, 0.13),
    inset -1px -1px 0px rgba(5, 17, 57, 0.1);
  border-radius: 8px;
  color: white;
`;

const TitleWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const CovidBlock = styled.div`
  width: 100%;
  padding: 20px;
  background: url("/globaladmissions/images/slides/slide1.png");
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 20px;
  line-height: 28px;
  span {
    color: #dbeafe;
    font-weight: 600;
    font-size: 20px;
    line-height: 28px;
  }
`;

const CategoryWrapper = styled.div`
  display: flex;
  text-align: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0px 4px;
`;

const CategoryItem = styled.div`
  margin: 0px 10px;
`;
const CategoryBox = styled.div`
  height: 100px;
  width: 100px;
  background: rgba(0, 0, 0, 0.09);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  background-size: cover;
`;
const CategoryDescription = styled.div`
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #334155;
`;

const Title = styled.div`
  font-size: 20px;
  font-weight: 700;
  border-radius: 6px;
  margin: 1px 0px;
  font-weight: 600;
  font-size: 24px;
  line-height: 32px;
  color: #334155;
`;
